/**
 * CSS helper classes
 */
.clearfix:before, .clearfix:after {
  content: ' ';
  display: table;
}

.clearfix:after {
  clear: both;
}

.invisible {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.img-responsive {
  height: auto;
  max-width: 100%;
  display: block;
}

.text-hide {
  text-indent: -9999px;
  overflow: hidden;
  display: block;
}

